import React, { Component } from "react";
import "./Sidebar.css";

class Sidebar extends Component {
  constructor(props) {
    super(props);

    // we put on state the properties we want to use and modify in the component
    this.state = {
      numberOfGuests: this.props.model.getNumberOfGuests(),
      selectedMenu: this.props.model.getFullMenu()
    };
  }

  // this methods is called by React lifecycle when the
  // component is actually shown to the user (mounted to DOM)
  // that's a good place to setup model observer
  componentDidMount() {
    this.props.model.addObserver(this);
  }

  // this is called when component is removed from the DOM
  // good place to remove observer
  componentWillUnmount() {
    this.props.model.removeObserver(this);
  }

  // in our update function we modify the state which will
  // cause the component to re-render
  update() {
    this.setState({
      numberOfGuests: this.props.model.getNumberOfGuests()
    });
  }

  // our handler for the input's on change event
  onNumberOfGuestsChanged = e => {
    this.props.model.setNumberOfGuests(e.target.value);
  };

  render() {
    return (
      <div  id="SidebarView" class="col-xs-12 col-md-3 navbar-default" role="navigation" >
			  <div class="navbar-header w-100">
			      <h3>My Dinner</h3>
			       <button type="button" class="navbar-toggle" data-toggle="collapse" data-target=".navbar-collapse">
			       <span class="sr-only">Toggle navigation</span>
			           <span class="glyphicon glyphicon-list"></span>
			       </button>
			  </div>

        <div class="navbar-collapse collapse">
          <span style="margin-right: 20px">People</span>
          <select id="guestNumber">
              <option value="1" selected onChange={this.onNumberOfGuestsChanged}>1</option>
              <option value="2" onChange={this.onNumberOfGuestsChanged}>2</option>
              <option value="3" onChange={this.onNumberOfGuestsChanged}>3</option>
              <option value="4" onChange={this.onNumberOfGuestsChanged}>4</option>
              <option value="5" onChange={this.onNumberOfGuestsChanged}>5</option>
          </select>
          <div class="col-xs-12" style="background-color: #eee; margin-top: 20px;">
            <p style="float: left;">Dish Name</p>
            <p style="float: right;">Price</p>
          </div>
          <div id="selectedDish">

          </div>
          <div style="width:100%; margin-top: 40px;text-align: right;border-top-style: solid;">
            <p><span id="totalPrice"></span> SEK</p>
          </div>
          <div style="margin: 0px auto;text-align: center;">
            <input id="ConfirmDinner" type="button" class="btn btn-lg" disabled value="Confirm Dinner" style="margin-top: 30px; background: #A9A9A9;"/>
          </div>
          <Link to="/overview">
            <button>Create Menu</button>
          </Link>

			    </div>
			</div>
    );
  }
}

export default Sidebar;



